import axios from 'axios'

//local
const API_URL = "https://d1e8-211-176-219-28.ngrok-free.app/gotcha/real-estate"

//prod
//const API_URL = "/gotcha/real-estate"

const getRealEstateList = async (pageNum, pageSize, searchText, sizeFilters, sortBy, sortOrder, minGap, maxGap) => {
    try{
        const response = await axios.get(API_URL, {
            headers: {
                'ngrok-skip-browser-warning': 'true'
            },
            params : {
                pageNum,
                pageSize,
                searchText,
                sizeFilters,
                sortBy,
                sortOrder,
                minGap,
                maxGap
            }
        })
        console.log("response : ",response.data)
        return response.data
    }catch(error){
        console.error("There was an error fetching the data list", error);
        throw error;
    }
}

export default getRealEstateList;
