import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import ItemTable from './ItemTable';
import Header from './Header';
import Search from './Search';
import Login from './Login';
import Signup from './Signup';
import './GotCha.css';
import { useEffect, useState } from 'react';
import getRealEstateList from './RealEstateService';
import PaginationComponent from './PaginationComponent';

function GotCha(){
    return(
        <Router>
            <Routes>
                <Route path="/" element={<MainContent/>} />
                <Route path="/login" element={<Login/>} />
                <Route path="/signup" element={<Signup/>} />
            </Routes>
        </Router>
    )
}

function MainContent() {

    const [inputValue, setInputValue] = useState('')
    const [searchResults, setSearchResults] = useState([])
    const [loading, setLoading] = useState(true);
    const [sortConfig, setSortConfig] = useState({ key: 'itemNumber', direction: 'DESC'});
    const [pageNum, setPageNum] = useState(1);
    const [pageMax, setPageMax] = useState(1);
    const [filterValues, setFilterValues] = useState([]);
    const [minGap, setMinGap] = useState(null);
    const [maxGap, setMaxGap] = useState(null);
    const [gapRange, setGapRange] = useState([0, 50]);
    
    const fetchData = async(pageNum, searchTerm, sortField, sortDirection, sizeFilter, minGap, maxGap) => {
        setLoading(true);
    
        const sizeFilterParam = sizeFilter && sizeFilter.length > 0 ? sizeFilter.join() : null;
        console.log("fetchData- pageNum : ",pageNum, ", searchTerm : ",searchTerm,", sortField : ",sortField, " , sortDirection : ",sortDirection, " , sizeFilter : ", sizeFilterParam, " , minGap : ", minGap, " , maxGap : ", maxGap)
        try{
            const data = await getRealEstateList(pageNum, 25, searchTerm, sizeFilterParam, sortField, sortDirection, minGap, maxGap);
            console.log("real estates : ",data.realEstates)
            setSearchResults(data.realEstates);
            setPageMax(Math.ceil(data.totalCount/25));
            setLoading(false);
        }catch(error){
            console.error("Error fetching data", error);
            setLoading(false);
        }
    };

    useEffect(() => {
        console.log("useEffect!! : ", inputValue, " sort: ", sortConfig)
        fetchData(pageNum, inputValue =='' ? null : inputValue, sortConfig.key, sortConfig.direction, filterValues, minGap, maxGap)
    },[])

    const handleSearchClick = async () => {
        console.log("handleSearchClcik!! input : ", inputValue, " sort: ", sortConfig)
        setPageNum(1)
        setSortConfig({ key: 'itemNumber', direction: 'DESC'})
        fetchData(1, inputValue == '' ? null : inputValue, 'itemNumber', 'DESC', filterValues, minGap, maxGap);
    }

    const handleSortChange = async (field, direction) => {
        console.log("handleSortClcik!! input : ", inputValue, " sort: ", sortConfig)
        setPageNum(1)
        fetchData(1, inputValue == '' ? null : inputValue, field, direction, filterValues, minGap, maxGap);
    }

    const handleFilterChange = async (field) => {
        console.log("handleFilterChange!! field : ", field)
        setPageNum(1)
        fetchData(1, inputValue == '' ? null : inputValue, sortConfig.key, sortConfig.direction, filterValues, minGap, maxGap);
    }

    const handlePageChange = async (event, value) => {
        console.log("handlePageChange page number : ", value)
        setPageNum(value);
        fetchData(value, inputValue == '' ? null : inputValue, sortConfig.key, sortConfig.direction, filterValues, minGap, maxGap)
    }

    const handleGapRangeChange = (value) => {
        console.log("handleGapRangeChange -> minGap : ", value[0], " maxGap : ", value[1])
        const newMinGap = value[0] * 100000000;
        const newMaxGap = value[1] * 100000000;
    
        setMinGap(newMinGap);
        setMaxGap(newMaxGap);
        setPageNum(1)
        fetchData(1, inputValue == '' ? null : inputValue, sortConfig.key, sortConfig.direction, filterValues, newMinGap, newMaxGap);
    }

    const handleTitleClick = () => {
        setInputValue('')
        setSearchResults([])
        setLoading(true)
        setPageNum(1)
        setSortConfig({ key: 'itemNumber', direction: 'DESC'})
        setFilterValues([])
        setGapRange([0, 50])
        setMinGap(null)
        setMaxGap(null)

        fetchData(1, null, 'itemNumber', 'DESC', [], null, null)
    }

    return (
        <>
            <Header onTitleClick={handleTitleClick}/>
            <Search
                inputValue={inputValue}
                setInputValue={setInputValue}
                handleSearchClick={handleSearchClick}
            />
            {loading ? (
                <div className="loading-container">
                    Loading
                </div>
            )
            : (
            <ItemTable
                datas={searchResults}
                sortConfig={sortConfig}
                setSortConfig={setSortConfig}
                onSortChange={handleSortChange}
                onFilterApply={handleFilterChange}
                setFilterValues={setFilterValues}
                filterValues={filterValues}
                setGapRangeValues={handleGapRangeChange}
                gapRange={gapRange}
                setGapRange={setGapRange}
            />
        )}
            <PaginationComponent
                pageMax={pageMax}
                pageNum={pageNum}
                handlePageChange={handlePageChange}
            />        
        </>
    );
}

export default GotCha;